.bars {
  padding: 2rem;
}
.firstbar {
  display: inherit;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 25vh;
  padding: 1rem;
  width: 20vw;

  box-shadow: 5px 5px 20px 0.5px rgba(0, 0, 0, 0.178);
  border-radius: 1rem;
}

