.bars {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 2rem;
  align-items: center;
  margin-left: 1rem;
  margin-right: 1rem;
}

.firstbar > h3 {
  padding: 0.5rem;
}

.secondbar > h3 {
